import * as React from "react";
import clsx from "clsx";

type GradientTextProps = {
  text: string;
  choice: number;
  className?: string;
};

const Gradients = [
  "from-green-300 via-blue-500 to-purple-600",
  "from-pink-500 via-red-500 to-yellow-500",
  "from-indigo-300 via-red-300 to-yellow-300",
  "from-red-300 via-red-300 to-yellow-400",
  "from-green-200 via-green-300 to-blue-500",
  "from-rose-400 via-fuchsia-500 to-indigo-500",
  "from-teal-200 to-lime-200",
  "from-violet-300 to-violet-400",
  "from-violet-500 to-orange-300",
];

export function GradientText({ choice, text, className }: GradientTextProps) {
  return (
    <span
      className={clsx(
        className,
        "bg-gradient-to-r text-transparent bg-clip-text",
        Gradients[choice]
      )}
    >
      {text}
    </span>
  );
}
