import React, { useContext } from "react";
import { graphql, useStaticQuery } from "gatsby";
import clsx from "clsx";
import { GatsbyImage, getImage } from "gatsby-plugin-image";

export const InvoiceMakerMLProject = () => {
  const [selectedIndex, setSelectedIndex] = React.useState<number>(0);
  const data = useStaticQuery(graphql`
    query {
      allFile(
        filter: { relativePath: { regex: "/projects/invoice-maker/.*/" } }
      ) {
        nodes {
          relativePath
          childImageSharp {
            gatsbyImageData(placeholder: BLURRED, formats: [AUTO, WEBP, AVIF])
          }
        }
      }

      thumbnails: allFile(
        filter: { relativePath: { regex: "/projects/invoice-maker/.*/" } }
      ) {
        nodes {
          relativePath
          childImageSharp {
            gatsbyImageData(
              width: 200
              placeholder: BLURRED
              formats: [AUTO, WEBP, AVIF]
            )
          }
        }
      }
    }
  `);

  const images = data.allFile.nodes;
  const gatsbyImages = images.map((image) => getImage(image));

  const thumbnailImages = data.thumbnails.nodes;
  const thumbnailGatsbyImages = thumbnailImages.map((image) => getImage(image));
  console.log(gatsbyImages[selectedIndex]);

  const frontend = [
    "Python",
    "Jupyter",
    "TensorFlow",
    "Flask",
    "OpenCV",
    "VoTT",
  ];

  return (
    <div className="">
      <div className="">
        {/* Header */}
        <div className="w-full">
          <p className="text-3xl font-bold text-center text-dark">
            Invoice Maker
          </p>
          <p className="font-light text-center text-amber-400">
            Image to Invoice using CNN
          </p>
        </div>
        <div className="flex justify-center px-5 py-3 space-x-2 broder bg-gray-50">
          <div className="flex flex-wrap justify-center max-w-4xl space-x-2 space-y-2">
            {frontend.map((x) => (
              <div className="px-4 py-2 text-sm bg-gray-100 broder whitespace-nowrap">
                {x}
              </div>
            ))}
          </div>
        </div>

        {/* BigImage | Description */}
        <div className="flex my-16 space-x-8 mx-9">
          <div className="flex-1 w-full ">
            <GatsbyImage
              objectFit={"contain"}
              image={gatsbyImages[selectedIndex]}
              alt={""}
              className="h-lg"
            />
          </div>
          <div className="flex-1 w-full space-y-4">
            <p className="text-lg">
              A machine learning solution to convert well laid out picture of
              products to a delivery invoice with supplementary features to aid
              management. This is one of my favorite projects, I have used Deep
              Convolutional Neural Network Model inspired from{" "}
              <GradientText choice={4} text="GoogLeNet" className="font-bold" />
              . The project uses Dentistry Products, however the project can be
              extended to any other sector. Its trained on 14 different products
              of contrasting visual features.
            </p>
            <p className="text-lg">
              The program is build using
              <GradientText
                choice={2}
                className="font-bold"
                text="TensorFlow and OpenCV"
              />{" "}
              I built custom dataset tailored for this project using image
              segmentation and Annotation tool called
              <GradientText
                choice={3}
                className="font-bold"
                text="VoTT by Microsoft"
              />
              .
            </p>
            <p className="text-lg">
              I had collected over 100 Images and 10s of videos from smartphone
              and annotated them using VoTT. Along with this, the training phase
              would generate many different variations of images using Data
              Augmentation. So in total i had over 1400 annotated image, which
              in the world of CNN was very little. Later. The detection process
              used OpenCV to segment the object using various traditional
              approaches like Edge Detection, Contour detection to segment the
              object in image, which are later fed to neural engine as input and
              a label is expected as output.
            </p>
            <p className="text-lg"></p>
            <p className="text-lg">
              Traditionl 'naive' CNN would obviously overfit my data and fail to
              generalize for new images. I had tried mixing, truncating and
              other variations of leading CNN models like VGG, ResNet, and
              other. In the end <GradientText choice={5} text="GoogLeNet " />{" "}
              yielded the best result, the Inception blocks proposed in
              GoogLeNet paper, worked wonders for small dataset of mine.
            </p>
          </div>
        </div>

        {/* Thumbnails */}

        <div className="flex mx-20 my-8 space-x-4">
          {thumbnailGatsbyImages.map((image, idx) => (
            <div
              className="flex items-center justify-center rounded-lg shadow-md"
              onClick={() => {
                console.log(idx);
                setSelectedIndex(idx);
              }}
              key={idx}
            >
              <GatsbyImage image={image} alt={""} />
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

type GradientTextProps = {
  text: string;
  choice: number;
  className?: string;
};

const Gradients = [
  "from-green-300 via-blue-500 to-purple-600",
  "from-pink-500 via-red-500 to-yellow-500",
  "from-indigo-300 via-red-300 to-yellow-300",
  "from-red-300 via-red-300 to-yellow-400",
  "from-green-200 via-green-300 to-blue-500",
  "from-rose-400 via-fuchsia-500 to-indigo-500",
  "from-teal-200 to-lime-200",
  "from-violet-300 to-violet-400",
  "from-violet-500 to-orange-300",
];

function GradientText({ choice, text, className }: GradientTextProps) {
  return (
    <span
      className={clsx(
        className,
        "bg-gradient-to-r text-transparent bg-clip-text",
        Gradients[choice]
      )}
    >
      {text}
    </span>
  );
}
