import React, { useContext } from "react";
import { graphql, useStaticQuery } from "gatsby";
import clsx from "clsx";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import { GradientText } from "../../../common/GradientText";

export const OmegaDentalProject = () => {
  const [selectedIndex, setSelectedIndex] = React.useState<number>(0);
  const data = useStaticQuery(graphql`
    query {
      allFile(
        filter: { relativePath: { regex: "/projects/omega-dental-app/.*/" } }
      ) {
        nodes {
          relativePath
          childImageSharp {
            gatsbyImageData(placeholder: BLURRED, formats: [AUTO, WEBP, AVIF])
          }
        }
      }

      thumbnails: allFile(
        filter: { relativePath: { regex: "/projects/omega-dental-app/.*/" } }
      ) {
        nodes {
          relativePath
          childImageSharp {
            gatsbyImageData(
              width: 200
              placeholder: BLURRED
              formats: [AUTO, WEBP, AVIF]
            )
          }
        }
      }
    }
  `);

  const images = data.allFile.nodes;
  const gatsbyImages = images.map((image) => getImage(image));

  const thumbnailImages = data.thumbnails.nodes;
  const thumbnailGatsbyImages = thumbnailImages.map((image) => getImage(image));
  console.log(gatsbyImages[selectedIndex]);

  const frontend = [
    "Native Android",
    "Kotlin",
    "Coroutines",
    "Flows",
    "ROOM Database",
    "Clean Architecture",
    "Multi-Module",
    "Glide",
    "Firebase Firestore",
    "Firebase Cloud Function",
    "Firebase Storage",
  ];

  return (
    <div className="my-14">
      <div className="">
        {/* Header */}
        <div className="w-full">
          <p className="text-3xl font-bold text-center text-dark">
            Omega Dental App
          </p>
          <p className="font-light text-center text-amber-400">
            Ecommerce solution for Dentistry
          </p>
        </div>
        <div className="flex justify-center px-5 py-3 space-x-2 broder bg-gray-50">
          <div className="flex flex-wrap justify-center max-w-4xl space-x-2 space-y-2">
            {frontend.map((x) => (
              <div className="px-4 py-2 text-sm bg-gray-100 broder whitespace-nowrap">
                {x}
              </div>
            ))}
          </div>
        </div>

        {/* BigImage | Description */}
        <div className="flex my-16 space-x-8 mx-9">
          <div className="flex-1 w-full ">
            <GatsbyImage
              objectFit={"contain"}
              image={gatsbyImages[selectedIndex]}
              alt={""}
              className="h-lg"
            />
          </div>
          <div className="flex-1 w-full space-y-4">
            <p className="text-lg">
              <GradientText
                choice={5}
                text=" Omega Dental App "
                className="font-bold"
              />
              is an dental ecommerce app built for{" "}
              <GradientText
                choice={5}
                text="Omega Enterprises "
                className="font-bold"
              />
              . Its a fully functional ecommerce app for Dentistry. You can find
              the app{" "}
              <a
                href="https://play.google.com/store/apps/details?id=com.omega.cart"
                className="bg-blue-400"
              >
                HERE
              </a>
              .
            </p>
            <p className="text-lg">
              The app is built using
              <GradientText
                choice={2}
                className="font-bold"
                text="Native Android"
              />{" "}
              based on material design principles. The backed in built on
              <GradientText choice={3} className="font-bold" text="Firebase" />.
            </p>
            <p className="text-lg">
              The app Enables dental professionals to access dental products
              ranging from a vast variety of categories, such as Endodontics,
              Dentistry material, Restoratives, Dental Equipments, Consumables,
              etc. It minimises the time and effort required to purchase
              products, particularly in dentistry which has enormous range of
              products from various brands. The UI is designed to be simple and
              interactive, it used smooth transitions and animations.
            </p>
            <p className="text-lg"></p>
            <p className="text-lg">
              The was my first android project, i had start working on this in
              early 2019. Initially, the project was written in Java, in
              mid-2020 i migrated the entire project to Kotlin. Which improved
              overall deveopment time, drastically reduced bugs, also it enabled
              access to modern development tools like Flows and Coroutines in
              Kotlin. The App is still actively used by doctors in Mumbai.
            </p>
          </div>
        </div>

        {/* Thumbnails */}

        <div className="flex mx-20 my-8 space-x-4">
          {thumbnailGatsbyImages.map((image, idx) => (
            <div
              className="flex items-center justify-center rounded-lg shadow-md"
              onClick={() => {
                console.log(idx);
                setSelectedIndex(idx);
              }}
              key={idx}
            >
              <GatsbyImage image={image} alt={""} />
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};
