import React, { useContext } from "react";
import { graphql, useStaticQuery } from "gatsby";
import clsx from "clsx";
import { GatsbyImage, getImage } from "gatsby-plugin-image";

export const BrainvaultProject = () => {
  const [selectedIndex, setSelectedIndex] = React.useState<number>(0);
  const data = useStaticQuery(graphql`
    query {
      allFile(filter: { relativePath: { regex: "/projects/brainvault/.*/" } }) {
        nodes {
          relativePath
          childImageSharp {
            gatsbyImageData(placeholder: BLURRED, formats: [AUTO, WEBP, AVIF])
          }
        }
      }

      thumbnails: allFile(
        filter: { relativePath: { regex: "/projects/brainvault/.*/" } }
      ) {
        nodes {
          relativePath
          childImageSharp {
            gatsbyImageData(
              width: 200
              placeholder: BLURRED
              formats: [AUTO, WEBP, AVIF]
            )
          }
        }
      }
    }
  `);

  const images = data.allFile.nodes;
  const gatsbyImages = images.map((image) => getImage(image));

  const thumbnailImages = data.thumbnails.nodes;
  const thumbnailGatsbyImages = thumbnailImages.map((image) => getImage(image));
  console.log(gatsbyImages[selectedIndex]);

  const frontend = ["Gatsby", "React", "Typescript", "Tailwinds.CSS"];
  const backend = [
    "Firebase Firestore",
    "Firebase Cloud Function",
    "AWS SES",
    "ListMonk",
  ];
  const testingLibs = ["Mocha", "Sinon", "Cypress", "Chai"];

  return (
    <div>
      <div className="">
        {/* Header */}
        <div className="w-full">
          <p className="text-3xl font-bold text-center text-dark">
            The Brain Vault Project
          </p>
          <p className="font-light text-center text-amber-400">
            Personal Coaching Startup
          </p>
        </div>
        <div className="flex space-x-2">
          <div className="px-5 py-3 space-x-2 broder bg-gray-50">
            <p className="text-xs text-center">Frontend</p>
            <div className="flex space-x-2">
              {frontend.map((x) => (
                <div className="px-4 py-2 text-sm bg-gray-100 broder">{x}</div>
              ))}
            </div>
          </div>
          <div className="px-5 py-3 space-x-2 broder bg-gray-50">
            <p className="text-xs text-center">Backend</p>
            <div className="flex space-x-2">
              {backend.map((x) => (
                <div className="px-4 py-2 text-sm bg-gray-100 broder">{x}</div>
              ))}
            </div>
          </div>
          <div className="px-5 py-3 space-x-2 broder bg-gray-50">
            <p className="text-xs text-center">Testing Libs</p>
            <div className="flex space-x-2">
              {testingLibs.map((x) => (
                <div className="px-4 py-2 text-sm bg-gray-100 broder">{x}</div>
              ))}
            </div>
          </div>
        </div>

        {/* BigImage | Description */}
        <div className="flex my-16 space-x-8 mx-9">
          <div className="flex-1 w-full ">
            <GatsbyImage image={gatsbyImages[selectedIndex]} alt={""} />
          </div>
          <div className="flex-1 w-full space-y-4">
            <p className="text-lg">
              The{" "}
              <a
                href="https://www.brainvault.in"
                target="_blank"
                className="text-blue-600 underline"
              >
                Brainvault
              </a>{" "}
              is a personal coaching startup with various offerring ranging from
              special Events, Coaching sessions, to courses and corporate plans.
            </p>
            <p className="text-lg">
              I built the web app that fullfills all the needs using best
              technologies.{" "}
              <GradientText choice={2} className="font-bold" text="Gatsby" /> is
              used for client site and{" "}
              <GradientText choice={3} className="font-bold" text="React" /> is
              used for admin. The backend is inspired from{" "}
              <GradientText
                choice={3}
                className="font-bold"
                text="serverless architecture"
              />
              , using{" "}
              <i>
                Firebase Cloud functions. It integrates our Payment gateway,
                Zoom meets, AWS SES, Authentication{" "}
              </i>
              . Serverless allowed faster development and a massive gain in
              infra costs and management.
            </p>
            <p className="text-lg">
              I've built the admin app in mind to enable live changes on
              website, which enables creating coaching sessions linked with any
              number of zoom accounts, full courses, events, on demand
              personalised discount coupons.
            </p>
            <p className="text-lg">
              Every page and component is well tested using industry leading
              testing frameworks, which covers needs from testing form fields
              and payment gateways to dispatching emails and newsletters.
            </p>
            <p className="text-lg">
              This was my longest and largest project that i have worked on in
              2021. The project took nearly 7 months for completion. In the end,
              it was well recieved by the founders and is now actively used by
              the users.
            </p>
          </div>
        </div>

        {/* Thumbnails */}

        <div className="flex mx-20 my-8 space-x-4">
          {thumbnailGatsbyImages.map((image, idx) => (
            <div
              className="flex items-center justify-center rounded-lg shadow-md"
              onClick={() => {
                console.log(idx);
                setSelectedIndex(idx);
              }}
              key={idx}
            >
              <GatsbyImage image={image} alt={""} />
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

type GradientTextProps = {
  text: string;
  choice: number;
  className?: string;
};

const Gradients = [
  "from-green-300 via-blue-500 to-purple-600",
  "from-pink-500 via-red-500 to-yellow-500",
  "from-indigo-300 via-red-300 to-yellow-300",
  "from-red-300 via-red-300 to-yellow-400",
  "from-green-200 via-green-300 to-blue-500",
  "from-rose-400 via-fuchsia-500 to-indigo-500",
  "from-teal-200 to-lime-200",
  "from-violet-300 to-violet-400",
  "from-violet-500 to-orange-300",
];

function GradientText({ choice, text, className }: GradientTextProps) {
  return (
    <span
      className={clsx(
        className,
        "bg-gradient-to-r text-transparent bg-clip-text",
        Gradients[choice]
      )}
    >
      {text}
    </span>
  );
}
