import React, { useContext } from "react";
import { graphql, useStaticQuery } from "gatsby";
import clsx from "clsx";
import { GatsbyImage, getImage } from "gatsby-plugin-image";

export const OmegaAdminProject = () => {
  const [selectedIndex, setSelectedIndex] = React.useState<number>(0);
  const data = useStaticQuery(graphql`
    query {
      allFile(filter: { relativePath: { regex: "/projects/admin-app/.*/" } }) {
        nodes {
          relativePath
          childImageSharp {
            gatsbyImageData(placeholder: BLURRED, formats: [AUTO, WEBP, AVIF])
          }
        }
      }

      thumbnails: allFile(
        filter: { relativePath: { regex: "/projects/admin-app/.*/" } }
      ) {
        nodes {
          relativePath
          childImageSharp {
            gatsbyImageData(
              width: 200
              placeholder: BLURRED
              formats: [AUTO, WEBP, AVIF]
            )
          }
        }
      }
    }
  `);

  const images = data.allFile.nodes;
  const gatsbyImages = images.map((image) => getImage(image));

  const thumbnailImages = data.thumbnails.nodes;
  const thumbnailGatsbyImages = thumbnailImages.map((image) => getImage(image));
  console.log(gatsbyImages[selectedIndex]);

  const frontend = [
    "Native Android",
    "Kotlin",
    "Coroutines",
    "Flows",
    "ROOM Database",
    "Clean Architecture",
    "Multi-Module",
    "Glide",
    "Firebase Firestore",
    "Firebase Cloud Function",
    "Firebase Storage",
  ];

  return (
    <div className="my-14">
      <div className="">
        {/* Header */}
        <div className="w-full">
          <p className="text-3xl font-bold text-center text-dark">
            Omega Administration Toolkit
          </p>
          <p className="font-light text-center text-amber-400">
            All-in-one management solution
          </p>
        </div>
        <div className="flex justify-center px-5 py-3 space-x-2 broder bg-gray-50">
          <div className="flex flex-wrap justify-center max-w-4xl space-x-2 space-y-2">
            {frontend.map((x) => (
              <div className="px-4 py-2 text-sm bg-gray-100 broder whitespace-nowrap">
                {x}
              </div>
            ))}
          </div>
        </div>

        {/* BigImage | Description */}
        <div className="flex my-16 space-x-8 mx-9">
          <div className="flex-1 w-full ">
            <GatsbyImage
              objectFit={"contain"}
              image={gatsbyImages[selectedIndex]}
              alt={""}
              className="h-lg"
            />
          </div>
          <div className="flex-1 w-full space-y-4">
            <p className="text-lg">
              The Toolkit is a solution for managing bussiness, it aids the
              <GradientText
                choice={5}
                text=" Omega Dental App "
                className="font-bold"
              />
              . Using same data-sources as online web site and android app,
              enables the app to propogate changes to all platforms at the same
              time. It supports multiple regions.
            </p>
            <p className="text-lg">
              I built the Android app to solve challenges faced by day to day
              working of business, the biggest challenge was to keep track of
              offline orders with online order from other sources. This mobile
              app tries to integrate all platforms under one app. The app is
              very small in size as it uses{" "}
              <GradientText
                choice={2}
                className="font-bold"
                text="Native Android"
              />{" "}
              which provides seamless native user experience. The Backened is
              built on{" "}
              <GradientText choice={3} className="font-bold" text="Firebase" />,
              heavily using Firestore and Cloud functions. It uses various Java
              Libraries like <GradientText choice={3} text="iTextPdf " />.
            </p>
            <p className="text-lg">
              Apart from usual CRUD bussiness features, the app provides various
              other features like generating and printing invoices, sharing
              products to various social medias, generating on demand
              personalised product catalogues, notifications service to the
              owner for every action performed by employees, visualize orders
              over google map, tracking payments.
            </p>
            <p className="text-lg">
              All this with the support for multi-region. Making it easier to
              expand business in different regions without the need to update
              app.
            </p>
            <p className="text-lg">
              This has been under active development since early 2020, and has
              registered 1000s of orders amounting to about 10 lac till now
              (Fall 2021).
            </p>
          </div>
        </div>

        {/* Thumbnails */}

        <div className="flex mx-20 my-8 space-x-4">
          {thumbnailGatsbyImages.map((image, idx) => (
            <div
              className="flex items-center justify-center rounded-lg shadow-md"
              onClick={() => {
                console.log(idx);
                setSelectedIndex(idx);
              }}
              key={idx}
            >
              <GatsbyImage image={image} alt={""} />
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

type GradientTextProps = {
  text: string;
  choice: number;
  className?: string;
};

const Gradients = [
  "from-green-300 via-blue-500 to-purple-600",
  "from-pink-500 via-red-500 to-yellow-500",
  "from-indigo-300 via-red-300 to-yellow-300",
  "from-red-300 via-red-300 to-yellow-400",
  "from-green-200 via-green-300 to-blue-500",
  "from-rose-400 via-fuchsia-500 to-indigo-500",
  "from-teal-200 to-lime-200",
  "from-violet-300 to-violet-400",
  "from-violet-500 to-orange-300",
];

function GradientText({ choice, text, className }: GradientTextProps) {
  return (
    <span
      className={clsx(
        className,
        "bg-gradient-to-r text-transparent bg-clip-text",
        Gradients[choice]
      )}
    >
      {text}
    </span>
  );
}
